.meme--container{
position :relative;
left:30%;
display:flex;
width: 550px;
height: 500px;
background-color: white;
}
.meme-textTop{
    box-sizing: border-box;

position: relative;
width: 230px;
height: 35px;
left: 36px;
top: 40px;
border: 1px solid #D5D4D8;
border-radius: 5px;
padding: 5px;
}

.meme-textBottom{
    box-sizing: border-box;

position: relative;
width: 230px;
height: 35px;
left: 70px;
top: 40px;

border: 1px solid #D5D4D8;
border-radius: 5px;
padding: 5px;
}

.meme-submit{
    position: absolute;
    width: 477px;
    height: 40px;
    left: 36px;
    top: 151px;
    color : white;
    background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
    border-radius: 5px;

}

.meme-generated{
    position : absolute;
    top : 45%;
    left : 12%;
    width : 400px;
    height : 250px;

}
.meme--topText{
    position : absolute;
    top : 44%;
    left : 30%;
    width : 200px;
    text-align : center;
    font-family: 'Impact';
font-style: normal;
font-weight: 900;
font-size: 32px;
color: #FFFFFF;
text-shadow: 0 0 3px #FF0000, 0 0 5px #0000FF;

}
.meme--bottomText{
    position : absolute;
    top : 80%;
